<template>
    <div id="main-content">
        <h1></h1>
        <div class="button-wrap" >
            <button v-on:click="sweetAlert()">get in touch!</button>
            <button v-on:click="sweetAlert2()">Want To Know More</button>
        </div>
    </div>
</template>

<script>

export default {
    data() {
       
    },
    methods: {
        sweetAlert() {
            this.$swal({ 
                title: '<a href="mailto:turner485@hotmail.co.uk"><i class="far fa-envelope"></i></a><a href="https://github.com/turner485"><i class="fab fa-github-square"></i></a>',
                type: '',
                html:'<a href="mailto:turner485@hotmail.co.uk"> ' +
                'Turner485@hotmail.co.uk</a> <div class="pipe"></div> <a href="https://github.com/turner485">@github.com/turner485</a>',
                showCloseButton: true,
                showCancelButton: false,
                focusConfirm: false,
                confirmButtonText:
                'Close',
            });
        }, 
        sweetAlert2() {
             this.$swal({
                title: '<i class="user fas fa-user-astronaut"></i></i>',
                type: '',
                html:'<span>Howdy Partner</span><p>My name is Benjamin, I am a full time web developer currently working in Ecommerce. the web development&nbsp;indistry.' +
                '  <span>Technologies include</span>HTML5, CCS3/SASS/LESS, JavaScript, GULP/GRUNT, Jquery, Vue, PHP,&nbsp;Wordpress.</p>',
                showCloseButton: true,
                showCancelButton: false,
                focusConfirm: false,
                confirmButtonText:
                'Close',
            });
        }
    }
}

var $message = '$benjamin_mcpolin'.split('').reverse();
var $timeout = 300;

var outputSlowly = setInterval(function() {
    
    $('#main-content h1').append($message.pop(),);
    
    if ($message.length === 0) {
        // No more characters. Break out.
        clearInterval(outputSlowly);   
    }
    
}, $timeout);

</script>
<style lang="scss">
@import "../sass/mixins.scss";
@import "../sass/styles.scss";
</style>




